import styled from "@emotion/styled";
import { Box } from "theme-ui";
import { transparentize } from "polished";

const swatches = {
  blue: "#0091FF",
  blueBright: "#3e8ff7",
  pink: "#D356B9",
  pinkGmi: "#EC5B94",
  red: "#FF562B",
  orange: "#D87C22",
  yellow: "#FFC503",
  green: "#1BB441",
  greenPrice: "#68CE81",
  bluePrice: "#50A7E9",
  redPrice: "#F55858",
  purple: "#7B61FF",
  black: "#000",
  white: "#FFF",
  "grey-200": "#F5F5F5",
  "grey-300": "#E4E4E4",
  "grey-400": "#C0C0C0",
  "grey-500": "#A7A7A7",
  "grey-600": "#7C7C7C",
  "grey-700": "#545454",
  "grey-800": "#231F20",
  "grey-900": "#151515",
  transparent: "transparent",
};

const colors = {
  ...swatches,
  /* Aliases */
  primary: swatches.blue,
  secondary: swatches.pink,
  background: swatches.black,
  error: swatches.red,
  text: swatches["grey-300"],
  disabled: swatches["grey-700"],
};

export default colors;

interface ProgressBarBaseProps {
  $percent: number;
  $bgColor?: keyof typeof colors;
}

interface ProgressBarBaseProps {
  $percent: number;
  $color?: keyof typeof colors;
  $bgColor?: keyof typeof colors;
}

export const ProgressBarBase = styled(Box)<ProgressBarBaseProps>`
  position: relative;
  height: 22px;
  width: 100%;

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 22px;
    width: 100%;
    background-image: linear-gradient(
        90deg,
        transparent,
        transparent 60%,
        ${colors[$bgColor]} 60%,
        ${colors[$bgColor]} 100%
      ),
      linear-gradient(
        0deg,
        #0091ff,
        ${({ theme }) => transparentize(0.65, "#0091FF")} 100%
      );
  }

  &::after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 22px;
    width: ${({ $percent }) => $percent + "%"};
    background-image: linear-gradient(
        90deg,
        transparent,
        transparent 60%,
        ${colors[$bgColor]} 60%,
        ${colors[$bgColor]} 100%
      ),
      linear-gradient(-2deg, #3e8ff7 15%, #ec5b94 65%, #ff5628 90%);
    background-size: cover;
  }
`;

export const CircleProgressBarBase = styled.div<ProgressBarBaseProps>`
  width: 100%;
  height: 100%;

  .circleContainer {
    --dot-diameter: 100%;
    --circle-border-width: 2px;
    --default-color: #545454;
    --completion-color: #f5f5f5;
    width: var(--dot-diameter);
    height: var(--dot-diameter);
    transform: rotate(-90deg);
  }
  .circleContainerBackground {
    fill: none;
    stroke: var(--default-color);
    stroke-width: var(--circle-border-width);
  }
  .circleOffset {
    fill: none;
    stroke-linecap: round;
    stroke: var(--completion-color);
    stroke-dasharray: 100 100;
    stroke-linecap: round;
    stroke-width: var(--circle-border-width);
    stroke-dashoffset: ${({ $percent }) => $percent};
  }
`;
